<template>
  <div class="super-flow-base-demo">
    <super-flow
      id="only"
      ref="superFlow"
      :node-list="nodeList"
      :link-list="linkList"
      :origin="origin"
      :graph-menu="graphMenuList"
      :node-menu="nodeMenuList"
      :link-menu="linkMenuList"
      :enter-intercept="enterIntercept"
      :output-intercept="outputIntercept"
      :link-desc="linkDesc">
      <template v-slot:node="{meta}">
        <div class="startNode" v-if="meta.prop === 'start'">
          <div class="inner">
            <h6>开始</h6>
          </div>
        </div>
        <div class="endNode" v-else-if="meta.prop === 'end'">
          <div class="inner">
            <h6>结束</h6>
          </div>
        </div>
        <div v-else :class="`flow-node otherNode`">
          <section>
            {{meta.desc}}
          </section>
        </div>
      </template>
    </super-flow>

    <el-dialog
      :title="drawerConf.title"
      :visible.sync="drawerConf.visible"
      :close-on-click-modal="false"
      width="500px">
      <el-form
        @keyup.native.enter="settingSubmit"
        @submit.native.prevent
        v-show="drawerConf.type === drawerType.node"
        ref="nodeSetting"
        :model="nodeSetting">
        <!-- <el-form-item
          label="节点名称"
          prop="name">
          <el-input
            v-model="nodeSetting.name"
            placeholder="请输入节点名称"
            maxlength="30">
          </el-input>
        </el-form-item> -->
        <el-form-item
          label="节点描述"
          prop="desc">
          <el-input
            v-model="nodeSetting.desc"
            placeholder="请输入节点描述"
            maxlength="30">
          </el-input>
        </el-form-item>
      </el-form>
      <el-form
        @keyup.native.enter="settingSubmit"
        @submit.native.prevent
        v-show="drawerConf.type === drawerType.link"
        ref="linkSetting"
        :model="linkSetting">
        <el-form-item
          label="连线描述"
          prop="desc">
          <el-input
            v-model="linkSetting.desc"
            placeholder="请输入连线描述">
          </el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button
          @click="drawerConf.cancel">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="settingSubmit">
          确 定
        </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>

  const drawerType = {
    node: 0,
    link: 1
  }
  import tsconfig from '../config/tsconfig.json'
  import data from '../config/interface.json'
  export default {
    data() {
      return {
        drawerType,
        drawerConf: {
          title: '',
          visible: false,
          type: null,
          info: null,
          open: (type, info) => {
            const conf = this.drawerConf
            conf.visible = true
            conf.type = type
            conf.info = info
            if (conf.type === drawerType.node) {
              conf.title = '节点'
              if (this.$refs.nodeSetting) this.$refs.nodeSetting.resetFields()
              this.$set(this.nodeSetting, 'name', info.meta.name)
              this.$set(this.nodeSetting, 'desc', info.meta.desc)
            } else {
              conf.title = '连线'
              if (this.$refs.linkSetting) this.$refs.linkSetting.resetFields()
              this.$set(this.linkSetting, 'desc', info.meta ? info.meta.desc : '')
            }
          },
          cancel: () => {
            this.drawerConf.visible = false
            if (this.drawerConf.type === drawerType.node) {
              this.$refs.nodeSetting.clearValidate()
            } else {
              this.$refs.linkSetting.clearValidate()
            }
          }
        },
        linkSetting: {
          desc: ''
        },
        nodeSetting: {
          name: '',
          desc: ''
        },

        origin: [681, 465],
        nodeList: [],
        linkList: [],

        graphMenuList: [
          [
            {
              label: '开始节点',
              disable(graph) {
                return !!graph.nodeList.find(node => node.meta.prop === 'start')
              },
              selected: (graph, coordinate) => {
                const start = graph.nodeList.find(node => node.meta.prop === 'start')
                if (!start) {
                  graph.addNode({
                    width: 100,
                    height: 80,
                    coordinate: coordinate,
                    meta: {
                      prop: 'start',
                      name: '开始节点'
                    }
                  })
                }
              }
            },
            {
              label: '条件节点',
              disable: false,
              selected: (graph, coordinate) => {
                graph.addNode({
                  width: 160,
                  height: 80,
                  coordinate: coordinate,
                  meta: {
                    prop: 'condition',
                    name: '条件节点'
                  }
                })
              }
            },
            {
              label: '审批节点',
              disable: false,
              selected: (graph, coordinate) => {
                graph.addNode({
                  width: 160,
                  height: 80,
                  coordinate: coordinate,
                  meta: {
                    prop: 'approval',
                    name: '审批节点'
                  }
                })
              }
            },
            {
              label: '抄送节点',
              disable: false,
              selected: (graph, coordinate) => {
                graph.addNode({
                  width: 160,
                  height: 80,
                  coordinate: coordinate,
                  meta: {
                    prop: 'cc',
                    name: '抄送节点'
                  }
                })
              }
            },
            {
              label: '结束节点',
              disable(graph) {
                return !!graph.nodeList.find(point => point.meta.prop === 'end')
              },
              selected: (graph, coordinate) => {
                graph.addNode({
                  width: 80,
                  height: 50,
                  coordinate: coordinate,
                  meta: {
                    prop: 'end',
                    name: '结束节点'
                  }
                })
              }
            }
          ],
          [
            {
              label: '打印数据',
              selected: (graph) => {
                let totalData = graph.toJSON();
                let {nodeList, linkList }= totalData
                this.formatNode(nodeList,linkList);
                this.formatLink(linkList);
                this.postData();
                this.setBackData();
                              
                console.log(nodeList);
                console.log(linkList);
                console.log(tsconfig); 
              }
            },
            {
              label: '全选',
              selected: (graph, coordinate) => {
                console.log(coordinate)
                graph.selectAll()
              }
            }
          ]
        ],
        nodeMenuList: [
          [
            {
              label: '删除',
              disable: false,
              hidden(node) {
                return node.meta.prop === 'start'
              },
              selected(node, coordinate) {
                console.log(coordinate)
                node.remove()
              }
            }
          ],
          [
            {
              label: '编辑',
              selected: (node, coordinate) => {
                console.log(coordinate)
                this.drawerConf.open(drawerType.node, node)
              }
            }
          ]
        ],
        linkMenuList: [
          [
            {
              label: '删除',
              disable: false,
              selected: (link, coordinate) => {
                console.log(coordinate)
                link.remove()
              }
            }
          ],
          [
            {
              label: '编辑',
              disable: false,
              selected: (link, coordinate) => {
                console.log(coordinate)
                this.drawerConf.open(drawerType.link, link)
              }
            }
          ]
        ]
      }
    },
    created() {},
    mounted(){
      let THIS = this;
      //默认数据
      THIS.nodeList = data.demo2;
      THIS.linkList = data.demo1;
      THIS.nodeList.map((item)=>{
        item.coordinate[0] = parseInt(item.coordinate[0]) - 600;
        item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
      });
      THIS.linkList.map((item)=>{
        item.startAt = [52.5, 55];
        item.endAt = [52.5, 0];
      });
      //接口数据
      fetch('http://localhost:8083/activitiModeler/service/model/7050005')
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        THIS.nodeList = myJson.nodeList;
        THIS.linkList = myJson.linkList; 
        THIS.nodeList.map((item)=>{
          item.coordinate[0] = parseInt(item.coordinate[0]) - 400;
          item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
        });
        THIS.linkList.map((item)=>{
          item.startAt = [52.5, 55];
          item.endAt = [52.5, 0];
        });
      });
    },
    methods: {
      enterIntercept(formNode, toNode, graph) {
        console.log(graph)
        const formType = formNode.meta.prop
        switch (toNode.meta.prop) {
          case 'start':
            return false
          case 'approval':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          case 'condition':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          case 'end':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          default:
            return true
        }
      },
      outputIntercept(node, graph) {
        console.log(graph)
        console.log(node)
        //let totalData = graph.toJSON();
        //let {nodeList, linkList }= totalData
        return !(node.meta.prop === 'end')
      },
      linkDesc(link) {
        return link.meta ? link.meta.desc : ''
      },
      settingSubmit() {
        const conf = this.drawerConf
        if (this.drawerConf.type === drawerType.node) {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.nodeSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.nodeSetting[key])
          })
          this.$refs.nodeSetting.resetFields()
        } else {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.linkSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.linkSetting[key])
          })
          this.$refs.linkSetting.resetFields()
        }
        conf.visible = false

        //格式化数据，回传服务器
      },
      resetConfigData(){
        tsconfig.childShapes = [];
      },
      formatNode(nodeList,linkList){
        nodeList.map((item)=>{
          let nextLine  = [];
            linkList.map((inner)=>{
              if(inner.startId === item.id){
                nextLine.push({
                  resourceId: item.id
                });
              }
            });
            tsconfig.childShapes.push({
              outgoing: nextLine,
              resourceId: item.id,
              bounds: {
                upperLeft: {
                x: item.coordinate[0],
                y: item.coordinate[1]
                },
                lowerRight: {
                  x: item.coordinate[0] + item.width,
                  y: item.coordinate[0] + item.height
                }
              },
              stencil: {
                id: 'UserTask'
              },
              dockers: [],
              properties: {
                formproperties: {
                  formProperties: []
                },
              multiinstance_collection: '',
              formkeydefinition: '',
              tasklisteners: {
                taskListeners: []
              },
              multiinstance_condition: '',
              executionlisteners: {
                executionListeners: []
              },
              documentation: '',
              prioritydefinition: '',
              multiinstance_cardinality: '',
              multiinstance_variable: '',
              usertaskassignment: '',
              multiinstance_type: 'None',
              exclusivedefinition: true,
              duedatedefinition: '',
              asynchronousdefinition: false,
              overrideid: item.id,
              name: item.meta.name,
              isforcompensation: 'false'
              },
              childShapes: []
            })
          });
      },
      formatLink(linkList){
        linkList.map((item)=>{
          tsconfig.childShapes.push({
            outgoing: [
              {
                resourceId: item.endId
              }
            ],
            resourceId: item.id,
            bounds: {
              upperLeft: {
                x: item.startAt[0],
                y: item.startAt[1]
              },
              lowerRight: {
                x: item.endAt[0],
                y: item.endAt[1]
              }
            },
            stencil: {
              id: 'SequenceFlow'
            },
            dockers: [],
            properties: {
              defaultflow: 'false',
              conditionsequenceflow: '${param1==1}',
              executionlisteners: '',
              documentation: '',
              overrideid: 'flow53',
              name: '受理'
            },
            childShapes: [],
            target: {
              resourceId: item.endId
            }
          })
        });
      },
      setBackData(){
        let src = 'http://localhost:8083/activitiModeler/editor?modelId=6217529&companyId=34&caseClass=1&processFlag=1&caseType=1&processType=&createUser=136&casePhaseNo=1,&agentPoint=1&processStatus=&flag='
        try {
          window.top.postMessage(tsconfig,src);
        } catch (error) {
          console.log(error);
        }
      },
      postData(){
        // test code
        let newParam = {
          dataJson:JSON.stringify(tsconfig),
          modelName:'测试123',
          description:'测试123',
          companyId:'34',
          caseClass:'1',
          caseType:'1',
          processFlag:'1',
          createUser:'136',
          casePhaseNo:'1',
          agentPoint:'1',
          status:'1'
        }
        // Update
        fetch(
          'http://localhost:8083/activitiModeler/service//model/save',{
          method:'POST',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body:JSON.stringify(newParam)
        })
        .then(response => response.json())
        .then(json => {
          console.log(json)
        })
      }
    } 
  }
</script>

